import React, { Component } from 'react';
import { BrowserRouter} from 'react-router-dom';
import './index.scss';
import Hamburger from '../Hamburger';
import OverlayMenu from '../OverlayMenu';
import DotsMenu from '../DotsMenu';
import ToggleQA from '../ToggleQA';

const qa = [
  '1824!',
  '1300/',
  '130072',
  '',
  '',
  '',
  '!',
  '72',
  ' :dreamercity@citywanderer.org'
]

class App extends Component {
  render() {
    return (
      <BrowserRouter>
        <div className="app">
          <Hamburger/>
          <OverlayMenu/>
          <img className="footer-logo" src="img/footer-logo.png" alt=""/>
          <section className="section-1">
            <DotsMenu/>
            <img className="key-visual" src="img/key-visual.png" alt=""/>
            <h2 className="slogan"></h2>
            <p className="subslogan"><br/><br/><br/></p>

            <h2 className="slogan -desktop"><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/></h2>
            <p className="subslogan -desktop"><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/><br/></p>
          </section>

          <section className="section-2" id="section-2">
            <div className="container">
              <img className="icon-section" src="img/icon-section-2.png" alt=""/>
              <img className="img-title" src="img/section-2-title.png" alt="" />
              <div className="left">
                <p className="paragraph">
                  <br/><br/><br/>
                </p>
                <div className="youtube">
                  <iframe width="560" height="315" src="https://www.youtube.com/embed/XkUboSHhwNU" frameBorder="0" allowFullScreen title="youtube"></iframe>
                </div>
                <p className="quote"></p>
                <p className="paragraph">72</p>
              </div>
              <div className="right">
                <img src="img/section-2-grid.png" alt="" className="grid"/>
                <a href="https://www.accupass.com/event/1707111229276298703140" className="button" target="_blank" rel="noopener noreferrer"><img src="/img/btn-brave.png" alt=""/></a>
              </div>
            </div>
          </section>

          <section className="section-3" id="section-3">
            <div className="container">
              <img className="icon-section" src="img/icon-section-3.png" alt=""/>
              <img className="img-title" src="img/section-3-title.png" alt="" />
              <div className="info">
                <p className="quote"></p>
                <p className="paragraph">
                </p>
                <img className="unplugged-goal" src="img/unplugged-goal.png" alt=""/>
                <div className="goal-box">
                  <p className="title"></p>
                  <p className="description paragraph">
                    <br/>?<br/><br/>
                  </p>
                  <p className="title"></p>
                  <p className="paragraph">!</p>
                  <p className="title"></p>
                  <p className="paragraph"></p>
                </div>
                <img className="icons" src="img/section-3-icons.png" alt=""/>
              </div>

              <div className="challange">
                <img className="content-image -m" src="img/challange-m.png" alt=""/>
                <img className="content-image -d" src="img/challange.png" alt=""/>
                <a href="https://www.accupass.com/event/1707111229276298703140" className="button" target="_blank" rel="noopener noreferrer"><img src="/img/btn-brave.png" alt=""/></a>
              </div>

            </div>
          </section>
          <section className="section-4" id="section-4">
            <div className="container">
              <img className="icon-section" src="img/icon-section-4.png" alt=""/>
              <img className="img-title" src="img/section-4-title.png" alt="" />

              {
                qa.map((q, i) => {
                  return (
                    <ToggleQA titleImg={`img/Q${i+1}-01.png`} key={i}>
                      {q}
                    </ToggleQA>
                  )
                })
              }
            </div>
          </section>
          <section className="section-5" id="section-5">
            <div className="container">
              <img className="icon-section" src="img/icon-section-5.png" alt=""/>
              <img className="img-title" src="img/section-5-title.png" alt="" />
              <div className="organization">
                <img className="logo" src="img/logo-gamania.png" alt=""/>
                <div className="info">
                  <img className="title" src="img/title-gamania.png" alt=""/>
                  <p className="paragraph">2009DRM400</p>
                </div>
              </div>
              <div className="organization">
                <img className="logo" src="img/logo-city-wanderer.png" alt=""/>
                <div className="info">
                  <img className="title" src="img/title-city-wanderer.png" alt=""/>
                  <p className="paragraph">2013Inspire people, cultivate the world49</p>
                </div>
              </div>
              <img src="img/section-5-slogan.png" alt="" className="slogan -d"/>
              <img src="img/section-5-slogan-m.png" alt="" className="slogan -m"/>
            </div>
          </section>
          <section className="section-6" id="section-6">
            <div className="container">
              <img className="icon-section" src="img/icon-section-6.png" alt=""/>
              <img className="img-title" src="img/section-6-title.png" alt="" />
              <img src="img/section-6-slogan.png" alt="" className="slogan -d"/>
              <img src="img/section-6-slogan-m.png" alt="" className="slogan -m"/>
            </div>
          </section>

        </div>
      </BrowserRouter>
    );
  }
}

export default App;



