const init = {
  isActive: false,
  sections: [
    '',
    '',
    '',
    '',
    ''
  ]
}

export default (state = init, action) => {
  switch (action.type) {
    case 'TOGGLE_OVERLAY_MENU':
      return {...state, isActive: !state.isActive}
      case 'CLOSE_OVERLAY_MENU':
        return {...state, isActive: false}
    default:
      return state
  }
}



